html.fp-enabled, .fp-enabled body
  margin: 0
  padding: 0
  overflow: hidden
  // /*Avoid flicker on slides transitions for mobile phones #336
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0)

#superContainer
  height: 100%
  position: relative
  // /* Touch detection for Windows 8
  -ms-touch-action: none
  // /* IE 11 on Windows Phone 8.1
  touch-action: none

.fp-section
  position: relative
  -webkit-box-sizing: border-box
  // /* Safari<=5 Android<=3
  -moz-box-sizing: border-box
  // /* <=28
  box-sizing: border-box

.fp-slide
  float: left
  height: 100%
  display: block

.fp-slidesContainer
  height: 100%
  display: block

.fp-slides
  z-index: 1
  height: 100%
  overflow: hidden
  position: relative
  -webkit-transition: all 0.3s ease-out
  // /* Safari<=6 Android<=4.3
  transition: all 0.3s ease-out

.fp-section.fp-table, .fp-slide.fp-table
  display: table
  table-layout: fixed
  width: 100%

.fp-tableCell
  display: table-cell
  vertical-align: middle
  width: 100%
  height: 100%

.fp-slidesContainer
  float: left
  position: relative

.fp-controlArrow
  position: absolute
  z-index: 4
  top: 50%
  cursor: pointer
  width: 0
  height: 0
  border-style: solid
  margin-top: -38px
  -webkit-transform: translate3d(0, 0, 0)
  -ms-transform: translate3d(0, 0, 0)
  transform: translate3d(0, 0, 0)
  &.fp-prev
    left: 15px
    width: 0
    border-width: 38.5px 34px 38.5px 0
    border-color: transparent #fff transparent transparent
  &.fp-next
    right: 15px
    border-width: 38.5px 0 38.5px 34px
    border-color: transparent transparent transparent #fff

.fp-scrollable
  overflow: scroll

.fp-notransition
  -webkit-transition: none !important
  transition: none !important

#fp-nav
  position: fixed
  z-index: 100
  margin-top: -32px
  top: 50%
  opacity: 1
  -webkit-transform: translate3d(0, 0, 0)
  &.right
    right: 17px
  &.left
    left: 17px

.fp-slidesNav
  position: absolute
  z-index: 4
  left: 50%
  opacity: 1
  &.bottom
    bottom: 17px
  &.top
    top: 17px

#fp-nav ul, .fp-slidesNav ul
  margin: 0
  padding: 0

#fp-nav ul li
  display: block
  width: 9px
  height: 9px
  margin: 20px
  position: relative

.fp-slidesNav ul li
  display: block
  width: 9px
  height: 9px
  margin: 7px
  position: relative
  display: inline-block

#fp-nav ul li a, .fp-slidesNav ul li a
  display: block
  position: relative
  z-index: 1
  width: 100%
  height: 100%
  cursor: pointer
  text-decoration: none

#fp-nav ul li a.active span, .fp-slidesNav ul li a.active span, #fp-nav ul li:hover a.active span, .fp-slidesNav ul li:hover a.active span
  height: 9px
  width: 9px
  border-radius: 100%
  background-color: #c29f77

#fp-nav ul li a span, .fp-slidesNav ul li a span
  border-radius: 50%
  position: absolute
  z-index: 1
  height: 9px
  width: 9px
  border: 0
  background: #999
  left: 0
  top: 0
  -webkit-transition: all 0.1s ease-in-out
  -moz-transition: all 0.1s ease-in-out
  -o-transition: all 0.1s ease-in-out
  transition: all 0.1s ease-in-out

#fp-nav ul li:hover a span, .fp-slidesNav ul li:hover a span
  width: 9px
  height: 9px
  background-color: #c29f77

#fp-nav
  ul li
    .fp-tooltip
      position: absolute
      top: -2px
      color: #fff
      font-size: 14px
      font-family: arial, helvetica, sans-serif
      white-space: nowrap
      max-width: 220px
      overflow: hidden
      display: block
      opacity: 0
      width: 0
    &:hover .fp-tooltip
      -webkit-transition: opacity 0.2s ease-in
      transition: opacity 0.2s ease-in
      width: auto
      opacity: 1
  &.fp-show-active a.active + .fp-tooltip
    -webkit-transition: opacity 0.2s ease-in
    transition: opacity 0.2s ease-in
    width: auto
    opacity: 1
  ul li .fp-tooltip
    &.right
      right: 20px
    &.left
      left: 20px

.fp-auto-height
  &.fp-section, .fp-slide, .fp-tableCell
    height: auto !important